<template>
  <div class="home">
    <h2>txt</h2>
    <a-upload-dragger
    name="file"
    accept="text/plain"
    :before-upload="beforeUpload"
    :remove="handleRemove"
    action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
    :multiple="true"
    @change="handleChange"
  >
    <p class="ant-upload-drag-icon">
      <a-icon type="inbox" />
    </p>
    <p class="ant-upload-text">
      Click or drag file to this area to upload
    </p>
    <p class="ant-upload-hint">
      Support for a single or bulk upload. Strictly prohibit from uploading company data or other
      band files
    </p>
  </a-upload-dragger>
  <a-button type="primary" class="export-table-btn" @click="exportTable" :loading="loading"></a-button>
  </div>
</template>

<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue'
import { Upload, Icon } from 'ant-design-vue'
// import { unique } from '../utils/array'
import xlsx from 'xlsx'

export default {
  name: 'TextToExcel',
  components: {
    [Upload.name]: Upload,
    [Upload.Dragger.name]: Upload.Dragger,
    [Icon.name]: Icon
  },
  data () {
    return {
      fileList: [],
      loading: false
    }
  },
  methods: {
    beforeUpload (file) {
      this.fileList = [...this.fileList, file]
      return false
    },
    handleRemove (file) {
      const index = this.fileList.indexOf(file)
      const newFileList = this.fileList.slice()
      newFileList.splice(index, 1)
      this.fileList = newFileList
    },
    handleChange (info) {
      const status = info.file.status
      console.log('handleChange', status)
      if (status !== 'uploading') {
        console.log(info.file, info.fileList)
      }
      if (status === 'done') {
        this.$message.success(`${info.file.name} file uploaded successfully.`)
      } else if (status === 'error') {
        this.$message.error(`${info.file.name} file upload failed.`)
      }
    },
    exportTable () {
      const tableHeader = {
        name: { title: '', regs: [/[:,](\S*)\r/] },
        gender: { title: '', regs: [/[:,](\S*)\r/] },
        age: { title: '', regs: [/[:,](\S*)\r/] },
        phone: { title: '', regs: [/1[:,](\S*)\r/, /2[:,](\S*)\r/, /[:,](\S*)\r/] },
        weight: { title: '', regs: [/\n(\S*)\r/] },
        adn: { title: '', regs: [/[:,](\S*)\r/] },
        nation: { title: '', regs: [/[:,](\S*)\r/] },
        nativePlace: { title: '', regs: [/[:,](\S*)\r/] },
        marriageStatus: { title: '', regs: [/[:,](\S*)\r/] },
        inHospitalDate: { title: '', regs: [/[:,](\S*)\r/] },
        pulse: { title: '', regs: [/\n(\S*)\r/] },
        stature: { title: '', regs: [/(\S*)\r/] },
        bp1: { title: '', regs: [/\r\n([0-9]{1,4})/] },
        bp2: { title: '', regs: [/([0-9]{1,4}mmHg)\r/] },
        mainArt: { title: '', regs: [/[:,](\S*)\r/] },
        historyPI: { title: '', regs: [/[:,](\S*)\r/] },
        historyITP: { title: '', regs: [/[:,]([\s\S]*)\n/] },
        historyPersonal: { title: '', regs: [/[:,]([\s\S]*)\n/] },
        historyHypertension: { title: '', regs: [/\n(\S*\S*)\r/] },
        historyCI: { title: '', regs: [/\n(\S*\S*)\r/] },
        historyHI: { title: '', regs: [/\n(\S*\S*)\r/] },
        historyDiabetes: { title: '', regs: [/\n(\S*\S*)\r/] },
        historyThyroid: { title: '', regs: [/\n(\S*\S*)\r/] },
        historyAnemia: { title: '', regs: [/\n(\S*\S*)\r/] },
        historySI: { title: '', regs: [/\n(\S*\S*)\r/] },
        DSA_CTA_MRA: { title: 'DSA_CTA_MRA', regs: [/\n(\S*[DSA,CTA,MRA]\S*)\r/] },
        initialDiagnosis: { title: '', regs: [/[:,]([\s\S]*)\n/] },
        operationName: { title: '', regs: [/[:,](\S*)\r/] },
        operationDate: { title: '', regs: [/[:,](\S*)\r/] },
        operationPhysician: { title: '', regs: [/[:,]([\s\S]*)\r/, /1[:,]([\s\S]*)\r/] },
        operationResult: { title: '', regs: [/[:,](\S*)\r/] },
        discharged: { title: '', regs: [/[:,](\S*)\r/] }
      }
      console.log(tableHeader)
      this.getTextArray().then(textArray => {
        const xlsxArray = []
        textArray.forEach((text, index) => {
          text = text.replace(/ /g, '')
          const item = {}
          for (const key in tableHeader) {
            if (Object.hasOwnProperty.call(tableHeader, key)) {
              const element = tableHeader[key]
              // console.log(element.title, regExecRes)
              for (let index = 0; index < element.regs.length; index++) {
                const reg = element.regs[index]
                const matchRes = text.match(reg)
                console.log(reg, 'reg', matchRes)
                if (matchRes && matchRes.length >= 2 && matchRes[1].length >= 1) {
                // matchRes = matchRes.filter(item => item && item.indexOf('\r') < 0)
                  item[element.title] = matchRes[1]
                  console.log(element.title, matchRes, 'hahah')
                  break
                } else {
                  item[element.title] = ''
                }
              }
            }
          }
          xlsxArray.push(item)
        })
        console.log('json ', xlsxArray)
        const ws = xlsx.utils.json_to_sheet(xlsxArray)
        const wb = xlsx.utils.book_new()
        xlsx.utils.book_append_sheet(wb, ws, '')
        xlsx.writeFile(wb, '.xlsx')
      })
    },
    getTextArray () {
      const fileList = this.fileList
      const readerTxt = this.readerTxt
      return new Promise(function (resolve, reject) {
        try {
          const textArray = []
          fileList.forEach(async (file, index) => {
            const result = await readerTxt(file)
            textArray.push(result)
            if (index === (fileList.length - 1)) {
              resolve(textArray)
            }
          })
        } catch (error) {
          reject(error)
        }
      })
    },
    readerTxt (file) {
      return new Promise(function (resolve, reject) {
        var reader = new FileReader()
        reader.onload = function () {
          if (reader.result) {
          // 
            // console.log(reader.result)
            resolve(reader.result)
          }
        }
        reader.onerror = function (e) {
          reject(reader.error)
          reader.abort()
        }
        reader.readAsText(file, 'gb2312')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.export-table-btn {
  margin-top: 10px;
}
</style>
